import axios from "axios"

export default {
  name: "HeaderHome",

  data() {
    return {
      showMenu: false,
      userConnected: false, 
      id: "",
      user: [],
      baseUrl: `https://production-api-rentable.onrender.com/users/getbyid`,

      
    }
  },

  async created ()  {
    
    this.id = localStorage.getItem("id")

    if(this.id) {
      this.userConnected = true
    }

    await this.getUser()
  },


  methods: {
    async getUser() {
      await axios.get(`${this.baseUrl}/${this.id}`)
      .then(res => {
        console.log(res)

        this.user = res.data 
      })
      .catch(err => {
        console.log(err)
      })
     }
  },
}