import axios from "axios"

export default {

  data () {
    return {
      id: localStorage.getItem("id"),
      baseUrl: `https://production-api-rentable.onrender.com/users/`,
      user: [],
      editbtn: false,
      newcontact: "",
      file: ""
     
    }
  },

  async mounted() {
     this.getUser()
  },

  methods: {
     async getUser() {
      await axios.get(`${this.baseUrl}getbyid/${this.id}`)
      .then(res => {
        console.log(res)
        this.user = res.data
       
      })
      .catch(err => {
        console.log(err)
      })
     },

     editButton() {
      this.editbtn = !this.editbtn
      
    },

    async handlePhoto(event) {
      this.file = event.target.files[0]
      console.log(this.file)
      this.newPhoto()
    },

    async newPhoto() {
      
      const formData = new FormData();
      formData.append("file", this.file)
      formData.append("_id", this.id)
      await axios.post(`${this.baseUrl}newphoto/`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        console.log(res.data)
        
        this.getUser()
      })
    },

   async save() {
      await axios.put(`${this.baseUrl}newcontact/${this.id}`, {
        contact: this.newcontact
      })

      .then(res => {
        console.log(res)
        this.getUser()
      })
    }
  },

  
}